import React from 'react'
import '../styles/notification.css'

const Notification = ({notificationMessage}) => {
    if (notificationMessage === null) {
        return null
    }

    const message = typeof notificationMessage === 'object' ? notificationMessage.error : notificationMessage
    
    const style = message.includes('Added') ? 'successMessage' : 'errorMessage'

    return (
        <div className={style}>
            {message}
        </div>
    )
}

export default Notification